import contact from "@/components/contact/contact.vue";
export default {
	components:{contact},
	data() {
		return {
			title:"",
			content:""
			
		}
	},
	onLoad() {
		this.getInfo();
	},
	methods: {
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		getInfo(){
			let _this = this;
			this.$ajax("/webcontent/info","POST",{code:"about"}).then(res=>{
				_this.title = res.title;
				_this.content = res.content;
			})
		}
	}
}
